import ajax from '@utils/ajax';

const serverPre = ajax.serverUrl;
const serverName = '/monitor-control/monitor/';

// 监控列表查询
export async function getMonitorlist(params) {
  return ajax.get(serverPre(`${serverName}querymonitorlist`), params);
}
// 新增监控配置
export async function addMonitor(params) {
  return ajax.post(serverPre(`${serverName}addmonitor`), params);
}
// 修改监控配置
export async function modifyMonitor(params) {
  return ajax.post(serverPre(`${serverName}modifymonitor`), params);
}
// 删除监控配置
export async function delMonitor(params) {
  return ajax.get(serverPre(`${serverName}delmonitor`), params);
}
// 监控信息详情
export async function monitorDetail(params) {
  return ajax.get(serverPre(`${serverName}querymonitordetail`), params);
}
// 停用/启用监控
export async function execMonitor(params) {
  return ajax.post(serverPre(`${serverName}execmonitor`), params);
}
