



















































































































































































































































































































































































import { Component, Vue } from 'vue-property-decorator';
import {
  getMonitorlist,
  delMonitor,
  execMonitor,
  addMonitor,
  modifyMonitor,
  monitorDetail
} from '../../api/monitor.js';
import decisionRuleEditor from '../processConfig/AddConfig/decisionRuleEditor.vue';
import { dataViewFields, linkList } from '../../api/viewData';
import rghMenu from '@components/RightMenu/RghMenu.vue';
import { userDictQuery } from '../../api/authority';
interface FormData {
  monitorName: string; //监控名称
  monitorType: any; //监控类型
  frequencyNum: string; //监控频率
  frequencyType: string; //监控频率类型
  deviationNum: string; //偏移值
  deviationType: string; //偏移值类型
  executionTime: string; //执行时间
  startDate: string; //开始日期
  endDate: string; //结束日期
  startTime: any; //开始时间
  endTime: any; //结束时间
  dataViewId: string; //目标视图id
  ruleArray: Array<any>; //过滤规则
  taskType: string; //任务类型
  recipient: string; //收件人
  copyRcipient: string; //抄送人
  title: string; //邮件主题/微信群组名
  msgTemplate: string; //消息模板
  fileFilter: string; //文件名称规则
}
@Component({
  components: { decisionRuleEditor, rghMenu }
})
export default class PageTemplate extends Vue {
  // @Ref() rghMenu: any;
  private title = '监控新增';
  private templateList: Array<any> = [];
  private selectForm: any = {
    monitorName: '',
    monitorType: ''
  };
  private monitorForm: any = {
    monitorType: ''
  };
  private loading = false;
  private currentPage = 1;
  private pageSize = 10;
  private total = 0;
  private templateId: any = '';
  private dialogVisible = false;
  private previewDialogVisible = false;
  private addVisible = false;
  private formLabelWidth = '25%';
  private dataViewList: Array<any> = [];
  private recipient: Array<any> = [];
  private copyRcipient: Array<any> = [];
  private serverConnectList: Array<any> = [];
  private canEdit = false; //控制监控名称可编辑
  private detailEdit = false; //查看详情按钮置灰
  private bottonShow = true;
  private state = 1;
  private editId = '';
  private all = true;

  private selectedMenu: any = {
    menuTitle: '',
    id: '',
    code: ''
  };
  private monitorTypeList: any = [
    {
      value: '01',
      label: '文件到达'
    },
    {
      value: '02',
      label: '外部监控结果读入'
    },
    {
      value: '03',
      label: '数据视图监控'
    }
  ];
  private monitorTypeSelect: any = [
    {
      value: 1,
      label: '文件到达'
    },
    {
      value: 2,
      label: '外部监控结果读入'
    },
    {
      value: 3,
      label: '数据视图监控'
    }
  ];
  private taskTypeList: any = [
    {
      id: 1,
      name: '微信通知'
    },
    {
      id: 2,
      name: '邮件通知'
    },
    {
      id: 0,
      name: '无'
    }
  ];
  private currentTemplate: any = null;
  private showError = false;
  private menuRules: any = {
    menuTitle: [{ message: '请输入菜单名称', trigger: 'blur', required: true }]
  };
  private timingform: FormData = {
    monitorName: '',
    frequencyNum: '',
    frequencyType: '',
    deviationNum: '',
    deviationType: '',
    dataViewId: '',
    ruleArray: [],
    executionTime: '',
    startDate: '',
    endDate: '',
    monitorType: '',
    taskType: '',
    recipient: '',
    copyRcipient: '',
    startTime: '',
    endTime: '',
    title: '',
    msgTemplate: '',
    fileFilter: ''
  };
  private frequencyTypeList: Array<any> = [
    { value: 8, label: '一次性' },
    { value: 7, label: '年' },
    { value: 6, label: '季' },
    { value: 5, label: '月' },
    { value: 4, label: '周' },
    { value: 3, label: '天' },
    { value: 2, label: '时' },
    { value: 1, label: '分' },
    { value: 0, label: '秒' }
  ];
  private deviationTypeList: Array<any> = [
    { value: 0, label: '工作日' },
    { value: 1, label: '自然日' }
  ];

  // 表格列
  columns() {
    return [
      {
        prop: '',
        type: 'index',
        align: 'center',
        label: '序号',
        width: '60'
      },
      {
        prop: 'monitorName',
        label: '监控名称',
        'header-align': 'left',
        showOverflowTooltip: true,
        width: '200'
      },
      {
        prop: 'monitorTypeName',
        label: '监控类型',
        'header-align': 'left',
        showOverflowTooltip: true,
        width: '160'
      },
      {
        prop: 'monitorStatus',
        label: '启用状态',
        align: 'center',
        width: '100',
        render(h: any, { record }: any) {
          let statusText = '';
          if (record.monitorStatus === 0) {
            statusText = '未启用';
          } else if (record.monitorStatus === 1) {
            statusText = '已启用';
          }
          return h('span', {}, statusText);
        }
      },
      {
        prop: 'monitorFreq',
        label: '调度频率',
        align: 'center',
        showOverflowTooltip: true
      },
      {
        prop: 'monitorTask',
        label: '后续任务',
        align: 'center',
        width: '120'
      },
      {
        prop: '',
        align: 'center',
        label: '操作',
        width: '200',
        actions: [
          {
            code: 'edit',
            text: '修改',
            onClick: this.modify
          },
          {
            code: 'copy',
            text: '详情',
            onClick: this.detail
          },
          {
            code: 'copy',
            text: '启用',
            onClick: this.use,
            hidden: (record) => record.monitorStatus !== 0
          },
          {
            code: 'copy',
            text: '禁用',
            onClick: this.use,
            hidden: (record) => record.monitorStatus !== 1
          },
          {
            code: 'delete',
            text: '删除',
            onClick: this.delete
          }
        ]
      }
    ];
  }

  // 按钮
  titleBtns() {
    const _this = this;
    return [
      {
        title: '新增',
        icon: 'add-line',
        onClick: _this.addPageTemplate
      }
    ];
  }
  // 删除空字段
  deleteEmptyParam(params) {
    for (const key in params) {
      if (
        params[key] === '' ||
        params[key] === undefined ||
        params[key] === null
      ) {
        delete params[key];
      }
    }
  }

  // 修改pageSize
  handleSize(val: any) {
    this.pageSize = val;
    this.currentPage = 1;
    this.getList();
  }

  // 页面跳转
  handleCurrent(val: any) {
    this.currentPage = val;
    this.getList();
  }

  // 查询按钮
  selectList() {
    this.currentPage = 1;
    this.getList();
  }
  // 重置
  resetQuery() {
    this.selectForm.monitorName = '';
    this.selectForm.monitorType = '';
    this.currentPage = 1;
    this.getList();
  }

  // 获取列表
  getList() {
    const param: any = {
      page: this.currentPage,
      pageSize: this.pageSize
    };
    if (this.selectForm.monitorName) {
      param.monitorName = this.selectForm.monitorName;
    }
    if (this.selectForm.monitorType) {
      param.monitorType = this.selectForm.monitorType;
    }
    this.loading = true;
    getMonitorlist(param).then(({ data, error }: any) => {
      this.loading = false;
      if (!error) {
        this.templateList = data.rows;
        this.total = data.total;
      } else {
        this.$message.error(error.message || '获取数据失败');
      }
    });
  }

  // 启用
  use(pageTemplate: any) {
    let userState = '';
    if (pageTemplate.monitorStatus === 0) {
      userState = '启用';
      this.state = 1;
    } else {
      userState = '禁用';
      this.state = 0;
    }
    this.$confirm(`是否要${userState}${pageTemplate.monitorName}?`, '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
      .then(() => {
        const param = {
          id: pageTemplate.id,
          monitorStatus: this.state
        };
        execMonitor(param).then(({ error }: any) => {
          if (!error) {
            this.$message.success(
              `${userState}${pageTemplate.monitorName}成功`
            );
            this.getList();
          } else {
            this.$message.error(error.message);
          }
        });
      })
      .catch(() => {});
  }

  // 删除监控
  delete(pageTemplate: any) {
    this.$confirm(`是否确定要删除${pageTemplate.monitorName}?`, '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
      .then(() => {
        const param = {
          id: pageTemplate.id
        };
        delMonitor(param).then(({ error }: any) => {
          if (!error) {
            this.$message.success(`删除${pageTemplate.monitorName}成功`);
            this.getList();
          } else {
            this.$message.error(error.message);
          }
        });
      })
      .catch(() => {});
  }

  // 新增
  addPageTemplate() {
    this.title = '监控新增';
    this.canEdit = false;
    this.addVisible = true;
    this.bottonShow = true;
    this.detailEdit = false;
  }
  // 修改监控配置
  modify(pageTemplate: any) {
    this.editId = pageTemplate.id;
    this.addVisible = true;
    this.canEdit = true;
    this.title = '监控编辑';
    this.detailEdit = false;
    this.bottonShow = true;
    this.lookDetail(pageTemplate.id);
  }
  // 监控配置查看详情
  detail(pageTemplate: any) {
    this.addVisible = true;
    this.canEdit = true;
    this.title = '监控详情';
    this.detailEdit = true;
    this.bottonShow = false;
    this.lookDetail(pageTemplate.id);
  }
  // 提交新增监控配置
  submitForm() {
    const param = {
      monitorName: this.timingform.monitorName, //监控名称
      monitorType: this.timingform.monitorType, //监控类型
      frequenceNum: this.timingform.frequencyNum, //监控频率
      frequenceType: this.timingform.frequencyType, //监控频率类型
      deviationNum: this.timingform.deviationNum, //偏移值
      deviationType: this.timingform.deviationType, //偏移类型
      startDate: this.timingform.startDate, //开始日期
      endDate: this.timingform.endDate, //结束日期
      startTime: this.timingform.startTime, //开始时间
      endTime: this.timingform.endTime, //结束时间
      recivceAdd: this.timingform.recipient, //收件人
      copier: this.timingform.copyRcipient, //抄送人
      monitorEndType: this.timingform.taskType, //后续任务类型
      title: this.timingform.title, //邮件主题/微信群组名
      msgTemplate: this.timingform.msgTemplate, //消息模板
      targetViewId: this.timingform.dataViewId, //数据视图规则
      fileFilter: this.timingform.fileFilter, //文件名称规则
      monitorFilter: '',
      id: ''
    };
    if (this.timingform.monitorName === '') {
      this.$message.error('请输入监控器名称');
      return false;
    }
    if (
      this.timingform.startDate.split('-').join('') >
      this.timingform.endDate.split('-').join('')
    ) {
      this.$message.error('结束日期必须大于开始日期');
      return false;
    }
    if (
      this.timingform.startTime.split(':').join('') >
      this.timingform.endTime.split(':').join('')
    ) {
      this.$message.error('结束时间必须大于开始时间');
      return false;
    }
    let rules;
    if (this.$refs.rules) {
      rules = this.$refs.rules;
      rules = { ...rules.getCurrentRule() };
    }
    if (this.timingform.monitorType === 2) {
      param.targetViewId = this.timingform.dataViewId;
    } else {
      if (rules && rules.dataViewId) {
        param.targetViewId = rules.dataViewId;
      }
    }
    if (rules && rules.ruleArray) {
      param.monitorFilter = rules.ruleArray;
    }
    this.deleteEmptyParam(param);
    if (this.title === '监控新增') {
      addMonitor(param).then(({ error }) => {
        if (!error) {
          this.$message.success('新增监控配置成功');
          this.addVisible = false;
          this.getList();
          this.cancel();
        } else {
          this.$message.error(`新增监控配置失败${error.message}`);
        }
      });
    } else {
      param.id = this.editId;
      modifyMonitor(param).then(({ error }) => {
        if (!error) {
          this.$message.success('修改监控配置成功');
          this.addVisible = false;
          this.getList();
          this.cancel();
        } else {
          this.$message.error(`修改监控配置失败${error.message}`);
        }
      });
    }
  }

  lookDetail(id) {
    const param = {
      id: id
    };
    monitorDetail(param).then(({ data }) => {
      if (data) {
        this.timingform.monitorName = data.monitorName;
        this.timingform.frequencyNum = data.frequenceNum;
        this.timingform.frequencyType = data.frequenceType;
        (this.timingform.startDate = data.startDate),
          (this.timingform.endDate = data.endDate),
          (this.timingform.deviationNum = data.deviationNum),
          (this.timingform.deviationType = data.deviationType),
          (this.timingform.executionTime = data.executionTime),
          (this.timingform.dataViewId = data.targetViewId),
          (this.timingform.ruleArray = data.monitorFilter);
        this.timingform.monitorType = data.monitorType;
        this.timingform.startTime = data.startTime;
        this.timingform.endTime = data.endTime;
        this.timingform.recipient = data.recivceAdd;
        this.timingform.copyRcipient = data.copier;
        this.timingform.title = data.title;
        this.timingform.taskType = data.monitorEndType;
        this.timingform.msgTemplate = data.msgTemplate;
        this.timingform.fileFilter = data.fileFilter;
      }
    });
  }

  // 取消
  cancel() {
    this.timingform.monitorName = '';
    this.timingform.monitorType = '';
    this.timingform.frequencyNum = '';
    this.timingform.frequencyType = '';
    this.timingform.deviationNum = '';
    this.timingform.deviationType = '';
    (this.timingform.startDate = ''),
      (this.timingform.deviationNum = ''),
      (this.timingform.deviationType = ''),
      (this.timingform.endDate = ''),
      (this.timingform.executionTime = ''),
      (this.timingform.dataViewId = ''),
      (this.timingform.taskType = ''),
      (this.timingform.recipient = ''),
      (this.timingform.copyRcipient = ''),
      (this.timingform.title = ''),
      (this.timingform.startTime = ''),
      (this.timingform.endTime = ''),
      (this.timingform.ruleArray = []);
    this.timingform.msgTemplate = '';

    this.addVisible = false;
  }
  // 获取数据视图筛选列表
  getViewDataOptions() {
    const param: any = {};
    param.type = '0';

    dataViewFields(param).then(({ data, error }) => {
      if (!error) {
        this.dataViewList = data;
      } else {
        this.$message.error(error.message);
      }
    });
  }
  getRecipient() {
    userDictQuery().then(({ data, error }) => {
      if (!error) {
        this.recipient = data;
        this.copyRcipient = data;
      }
    });
  }
  linkListData() {
    const linkListData = {
      page: 1,
      limit: 10000,
      type: '2',
      name: ''
    };
    linkList(linkListData).then(({ data, error }) => {
      if (!error) {
        this.serverConnectList = data.rows;
      }
    });
  }
  created() {
    this.getList();
    this.getViewDataOptions();
    this.getRecipient();
    this.linkListData();
  }
}
